.modal {
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  z-index: 3;

  display: flex;
  justify-content: center;
  align-items: center;

  .modal__mask {
    position: absolute;
    left: 0px;
    right: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.3);

    animation-name: modal-mask;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;
  }
  .modal__content {
    position: relative;
    background-color: white;
    color: $color-primary;
    padding: 20px 30px;
    width: calc(100vw - 60px - 16px);
    max-width: 1170px;
    // max-height: 80%;

    animation-name: modal-content;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;
  }

  .modal__btn-close {
    cursor: pointer;
    position: absolute;
    top: 0px;
    right: 0px;
    width: 44px;
    height: 44px;
    font-size: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba($color: #000000, $alpha: 0.5);
  }

  .modal__title {
    font-size: 1.5rem;
    padding: 24px 0px;
    text-align: center;
  }

  .modal__children {
    overflow-y: scroll;
    max-height: 450px;
  }

  .modal__children::-webkit-scrollbar {
    display: none;
  }
}

@keyframes modal-mask {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes modal-content {
  0% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1);
  }
}
