.project-content {
  display: grid;
  grid-template-columns: repeat(2, calc(50% - 15px));
  column-gap: 30px;

  .project-content__demo {
    
  }

  .project-content__content {
    @extend .text--light;
    text-align: left;
  }

  .project-content__section {
    margin-bottom: 36px;
  }

  .project-content__title {
    font-size: 1rem;
  }

  .project-content__text {
    margin-top: 12px;
    font-size: 0.8rem;
    color: rgba($color: #000000, $alpha: 0.7);

    li {
      padding: 9px 0px;
      border-bottom: solid 1px #eaeaea;
      display: flex;
    }

    ul {
      list-style: none;
      padding: 0px;
    }

    li:last-child {
      border-bottom: none;
    }

    i {
      margin-right: 9px;
    }
  }

  .project-content__head {
    margin-right: 5px;
  }

  .project-content__skill {
    display: flex;
    flex-wrap: wrap;
  }

  .project-content__tag {
    font-size: 0.65rem;
    line-height: 2;
    padding: 0px 10px;
    margin: 2px 2px;
    border-radius: 99px;
    background-color: $color-primary;
    color: $color-secondary;
  }

  .project-content__btn {
    @extend .text--color-secondary;
    @extend .btn;
    @extend .btn--primary;
    display: block;
    text-align: center;
    padding: 10px 20px;
  }
}

@media only screen and (max-width: 767.98px) {
  .project-content {
    grid-template-columns: 100%;

    .project-content__demo {
      border-bottom: solid 1px #eaeaea;
      margin-bottom: 24px;
    }
  }
}
