.skill-list {
  margin-top: 45px;

  .skill-list__content {
    display: grid;
    grid-template-columns: repeat(4, auto);
    column-gap: 20px;
  }
}

.skill-column {
  margin-top: 20px;

  .skill-column__title {
    text-transform: lowercase;
  }
  .skill-column__content {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 991.98px) {
  .skill-list {
    margin-top: 30px;
    .skill-list__content {
      grid-template-columns: repeat(2, auto);
    }
  }
  .skill-column {
    margin-top: 10px;
    .skill-column__title {
      text-align: left;
      margin-left: 5px;
    }
  }
}

@media only screen and (max-width: 767.98px) {
  .skill-list {
    margin-top: 15px;
    .skill-list__content {
      grid-template-columns: repeat(1, auto);
    }
  }
  .skill-column {
    margin-top: 5px;
  }
}
