$color-primary: #151719;
$color-secondary: #f2efe4;
$color-active: #08c;
$color-blue: #196eb3;
$color-indigo: #533b77;
$color-purple: #7348b6;
$color-pink: #c76395;
$color-red: #cb3837;
$color-orange: #e84f31;
$color-yellow: #c5b011;
$color-green: #86c03c;
$color-teal: #20c997;
$color-cyan: #08a9c5;
$color-white: #fff;
$color-gray: #6c757d;
$color-gray-dark: #343a40;
$space-content: 40px;
$space-content-sm: 20px;

html {
  font-family: "Exo", sans-serif;
  font-size: 19px;
  font-weight: 600;
  line-height: 1.2;
}

body {
  background-color: $color-primary;
  color: $color-secondary;
  margin: 0px;
}

body::-webkit-scrollbar {
  display: none;
}

p,
h1,
h2 {
  margin: 0px;
}

a {
  color: unset;
  text-decoration: unset;
}

@media only screen and (max-width: 991.98px) {
  html {
    font-size: 17px;
  }
}

@media only screen and (max-width: 767.98px) {
  html {
    font-size: 14px;
  }
}
