.navigator {
  position: fixed;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 30px $space-content;
  width: calc(100% - 2 * 40px);
  z-index: 2;
  transition: 0.1s ease-in;

  .navigator__item {
    margin: 0px 10px;
  }
  .navigator__item--header {
    margin-right: auto;
    h1 {
      font-size: 1rem;
    }
  }
  .navigator__item--link {
    cursor: pointer;
    font-size: 0.8rem;
  }
  .navigator__item--link.btn {
    padding: 5px 15px;
    border-radius: 100px;
  }

  .animation-element {
    transform: translate(0px, 10px);
  }
  .animation-element--start {
    transform: translate(0px, 0px);
  }
}

.navigator--active {
  background-color: $color-primary;
  padding: 20px $space-content;
}

@media only screen and (max-width: 767.98px) {
  .navigator {
    padding: $space-content-sm;
    width: calc(100% - 2 * 20px);
  }

  .navigator--active {
    opacity: 0;
    visibility: hidden;
  }
}
