.introduct {
  text-align: center;

  .introduct__greeting {
    font-size: calc(2vw + 2vh);
  }

  .introduct__location {
    font-size: calc(1.2vw + 1.2vh);
    margin-top: 10px;
  }

  .introduct__skill-content {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    column-gap: 20px;
  }
  .animation-element {
    transform: translate(0px, 20px);
  }
  .animation-element--start {
    transform: translate(0px, 0px);
  }
}
