.text--light {
  font-weight: 400;
}
.text--bold {
  font-weight: 600;
}
.text--color-primary {
  color: $color-primary;
}
.text--color-secondary {
  color: $color-secondary;
}
.text--color-active {
  color: $color-active;
}
.background--primary {
  background-color: $color-primary;
}
.background--secondary {
  background-color: $color-secondary;
}
.background--active {
  background-color: $color-active;
}
.btn {
  transition: 0.3s ease;
  cursor: pointer;
  @extend .text--color-secondary;
  @extend .background--primary;
}
.btn:hover {
  @extend .background--active;
}
.btn--blue:hover {
  background-color: $color-blue !important;
}
.btn--indigo:hover {
  background-color: $color-indigo !important;
}
.btn--purple:hover {
  background-color: $color-purple !important;
}
.btn--pink:hover {
  background-color: $color-pink !important;
}
.btn--red:hover {
  background-color: $color-red !important;
}
.btn--orange:hover {
  background-color: $color-orange !important;
}
.btn--yellow:hover {
  background-color: $color-yellow !important;
}
.btn--green:hover {
  background-color: $color-green !important;
}
.btn--teal:hover {
  background-color: $color-teal !important;
}
.btn--cyan:hover {
  background-color: $color-cyan !important;
}
.btn--white:hover {
  background-color: $color-white !important;
  color: $color-primary !important;
}
.btn--primary {
  @extend .background--active;
}
.btn--primary:hover {
  background-color: rgba($color: $color-active, $alpha: 0.7);
}
.underline-animation {
  position: relative;
}
.underline-animation::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0px;
  width: 100%;
  height: 2px;
  background-color: $color-active;
  transform: scaleX(0);
  transform-origin: left;
  transition: 0.3s ease;
}
.underline-animation:hover::after {
  transform-origin: right;
  transform: scaleX(1);
}
.underline-animation:hove::after {
  transform: scaleX(1);
}
.badge {
  @extend .text--light;
  @extend .btn;
  position: relative;
  font-size: 0.8rem;
  padding: 5px 8px;
  margin: 5px 10px;
  .badge__title {
    position: relative;
    z-index: 1;
  }
}
.badge--block {
  content: "";
  position: absolute;
  height: 50%;
  width: calc(100% + 10px);
  background-color: $color-primary;
  transition: 0.1s ease-out;
}
.badge::before {
  @extend .badge--block;
  top: 0px;
  left: -10px;
}
.badge::after {
  @extend .badge--block;
  bottom: 0px;
  right: -10px;
}
.badge:hover::before {
  left: 0px;
  width: 100%;
}
.badge:hover::after {
  right: 0px;
  width: 100%;
}
.badge--blue:hover::before,
.badge--blue:hover::after {
  background-color: $color-blue !important;
}
.badge--indigo:hover::before,
.badge--indigo:hover::after {
  background-color: $color-indigo !important;
}
.badge--purple:hover::before,
.badge--purple:hover::after {
  background-color: $color-purple !important;
}
.badge--pink:hover::before,
.badge--pink:hover::after {
  background-color: $color-pink !important;
}
.badge--red:hover::before,
.badge--red:hover::after {
  background-color: $color-red !important;
}
.badge--orange:hover::before,
.badge--orange:hover::after {
  background-color: $color-orange !important;
}
.badge--yellow:hover::before,
.badge--yellow:hover::after {
  background-color: $color-yellow !important;
}
.badge--green:hover::before,
.badge--green:hover::after {
  background-color: $color-green !important;
}
.badge--teal:hover::before,
.badge--teal:hover::after {
  background-color: $color-teal !important;
}
.badge--cyan:hover::before,
.badge--cyan:hover::after {
  background-color: $color-cyan !important;
}
.badge--gray:hover::before,
.badge--gray:hover::after {
  background-color: $color-gray !important;
}
.badge--white:hover::before,
.badge--white:hover::after {
  background-color: $color-white !important;
}
.badge--white:hover {
  .badge__title {
    color: $color-primary !important;
  }
}
.animation-element {
  transition: 0.3s ease;
  opacity: 0;
}
.animation-element--start {
  opacity: 1;
}
