.social {
  position: fixed;
  right: $space-content;
  bottom: $space-content;
  display: flex;
  flex-direction: column;

  .social__item {
    font-size: 21px;
    margin: 4px 0px;
    transition: 0.2s ease;
  }

  .social__item:hover {
    color: $color-active;
  }

  .animation-element {
    transform: translate(15px, 0px);
  }

  .animation-element--start {
    transform: translate(0px, 0px);
  }
}
