.project {
  text-align: center;

  .project__title {
    margin-bottom: 8px;
    font-size: calc(1.2vw + 1.2vh);
  }

  .project__desc {
    @extend .text--light;
    margin-bottom: 24px;
    font-size: 0.8rem;
  }

  .project__slider {
    margin: 0 auto;
    max-width: 1425px;
  }

  .animation-element {
    transform: translate(0px, 20px);
  }

  .animation-element--start {
    transform: translate(0px, 0px);
  }
}

.project-item {
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 256px;
  height: 256px;
  margin: 0 auto;
  border-radius: 4px;

  .project-item__img {
    height: 100%;
    object-fit: cover;
    transition: 0.3s ease;
  }

  .project-item__mask {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.3);
    opacity: 0;
    transition: 0.3s ease;
  }

  .project-item__info {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 50px;
    height: 50px;
    transform: scale(0);
    transform-origin: top right;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba($color: $color-primary, $alpha: 0.8);
    transition: 0.3s ease;
  }

  .project-item__desc {
    position: absolute;
    text-overflow: ellipsis;
    left: 0px;
    bottom: 30px;
    background-color: rgba($color: $color-primary, $alpha: 0.8);
    padding: 13px 21px 17px;
  }

  .project-item__tag {
    position: absolute;
    bottom: -15px;
    left: 21px;
    background-color: $color-active;
    padding: 6px 12px;
    font-size: 0.55rem;
    text-transform: uppercase;
  }
}

.project-item:hover {
  .project-item__img {
    transform: scale(1.15);
  }
  .project-item__info {
    transform: scale(1);
  }
  .project-item__mask {
    opacity: 1;
  }
}

.slick-dots li button:before {
  color: white;
}

.slick-dots li.slick-active button:before {
  color: $color-active;
}
