.mouse {
  position: absolute;
  bottom: 10px;
  left: calc(50% - 50px);
  width: 100px;
  transition: 0.2s ease;
  opacity: 0;
  cursor: unset;

  .fa {
    font-weight: 100;
    font-size: 10px;
    line-height: 0.7;
  }

  .mouse__arrow {
    position: absolute;
    left: calc(50% - 4.5px);
    animation-name: mouse;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-timing-function: ease;
  }
}

.mouse--active {
  cursor: pointer;
  opacity: 0.3;
}

@keyframes mouse {
  0% {
    bottom: -4px;
    opacity: 1;
  }
  30% {
    bottom: -8px;
    opacity: 0;
  }
  100% {
    bottom: -8px;
    opacity: 0;
  }
}
